export default Home;
"use client";
import React, { useRef, useEffect, useState } from "react";
import { motion, useMotionValue } from "framer-motion";

const navPhotos = {
  SHOP: ["/images/shop.jpg"],
  ACCOUNT: ["/images/home.jpg"],
  CART: ["/images/cart.webp"],
  CONTACT: ["/images/contact.jpeg"],
};

// Example clothes collection
const clothesCollection = [
  { name: "Black Tee", img: "/images/black1.jpeg" },
  { name: "Green Tee", img: "/images/green1.jpeg" },
  { name: "Red Tee", img: "/images/red1.jpeg" },
  { name: "White Tee", img: "/images/white1.jpeg" },
  { name: "Tshirt Plain", img: "/images/tshirt plain.jpeg" },
  { name: "Tshirt Back", img: "/images/tshirt back.jpeg" },
  { name: "Tshirt 1", img: "/images/tshirt1.jpeg" },
  { name: "Black Tee 2", img: "/images/black2.jpeg" },
  { name: "Green Tee 2", img: "/images/green2.jpeg" },
  { name: "Red Tee 2", img: "/images/red2.jpeg" },
  { name: "White Tee 2", img: "/images/white 2.jpeg" },
];

function ClothesScroller() {
  const scrollerRef = useRef(null);
  const x = useMotionValue(0);
  const speedRef = useRef(20); // px/sec

  // Auto-scroll effect
  useEffect(() => {
    let lastTime = performance.now();
  let frame: number;
  function animateScroll(now: number) {
      const dt = (now - lastTime) / 1000;
      lastTime = now;
      x.set(x.get() - speedRef.current * dt);
      frame = requestAnimationFrame(animateScroll);
    }
    frame = requestAnimationFrame(animateScroll);
  return () => cancelAnimationFrame(frame);
  }, [x]);

  // Speed up auto-scroll when user scrolls the page
  useEffect(() => {
    function handleScroll() {
      speedRef.current = 120;
      setTimeout(() => { speedRef.current = 20; }, 800);
    }
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // Drag interaction
  return (
    <div style={{ width: "100vw", overflow: "hidden", padding: "32px 0" }}>
      <motion.div
        ref={scrollerRef}
        drag="x"
        dragConstraints={{ left: -clothesCollection.length * 340 + window.innerWidth, right: 0 }}
        style={{ display: "flex", gap: "32px", x }}
      >
        {clothesCollection.map((item, idx) => (
          <motion.div
            key={item.name + idx}
            style={{
              minWidth: 320,
              height: 420,
              background: "#fff",
              borderRadius: 32,
              boxShadow: "0 8px 32px #bbb",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              overflow: "hidden",
              cursor: "grab",
            }}
            whileTap={{ scale: 0.97 }}
          >
            <img src={item.img} alt={item.name} style={{ width: 260, height: 320, objectFit: "cover", borderRadius: 24, marginBottom: 16 }} />
            <div style={{ fontWeight: "bold", fontSize: 22 }}>{item.name}</div>
          </motion.div>
        ))}
      </motion.div>
    </div>
  );
}
